.sm-btn {
  position: relative;
  display: block;
  margin: 0 auto;
  padding-left: 12px;
  padding-right: 12px;
  font-size: 18px;
  text-align: center;
  text-decoration: none;
  color: #fff;
  line-height: 2.55556;
  border-radius: 5px; }
  .sm-btn:active {
    color: rgba(255, 255, 255, 0.6); }

.sm-btn.primary {
  background-color: #f60; }
  .sm-btn.primary:active {
    background-color: #CE5D11; }

.sm-btn.warn {
  background-color: #E64340; }
  .sm-btn.warn:active {
    background-color: #CE3C39; }

.sm-btn.default {
  color: #000;
  background-color: #F8F8F8;
  border: 1px solid rgba(0, 0, 0, 0.1); }
  .sm-btn.default:active {
    color: rgba(0, 0, 0, 0.6);
    background-color: #DEDEDE; }

.sm-btn.plain-primary {
  color: #ff6600;
  border: 1px solid #ff6600; }
  .sm-btn.plain-primary:active {
    color: rgba(255, 102, 0, 0.6);
    border-color: rgba(255, 102, 0, 0.6); }

.sm-btn.disabled {
  color: rgba(255, 255, 255, 0.6);
  cursor: default; }
  .sm-btn.disabled.primary {
    background-color: #EEBC9E; }
  .sm-btn.disabled.warn {
    background-color: #EC8B88; }
  .sm-btn.disabled.default {
    color: rgba(0, 0, 0, 0.3);
    background-color: #F7F7F7; }
  .sm-btn.disabled.plain-primary {
    color: rgba(0, 0, 0, 0.3);
    border-color: rgba(0, 0, 0, 0.1); }

.sm-btn.mini {
  display: inline-block;
  padding: 0 1.32em;
  line-height: 2.3;
  font-size: 13px; }

button.sm-btn, input.sm-btn {
  width: 100%;
  border-width: 0;
  outline: 0;
  -webkit-appearance: none; }
  button.sm-btn:focus, input.sm-btn:focus {
    outline: 0; }

.sm-btn + .sm-btn {
  margin-top: 15px; }

